
.entry{
  margin-top:20px;
  display: block;
}
.entry .heading{
  font-size:1.0em;
  font-weight: bold;
  margin-bottom: 10px;
}
.entry input,textarea{
  width: 100%;
  border-radius: 2px;
  border: solid #dddddd 1px;
}
.btns{
  margin-top:50px;
  display: inline-block;
  width: 100%;
}
.btns input{
  float: right;
  margin-left: 20px;
  text-align: center;
  border-color: #007BFF;
  border-style: solid;
  border-radius: 3px;
  padding-top: 5px;
  padding-bottom:5px;
  padding-left:15px;
  padding-right: 15px;
}
.btns input:hover{
  background-color: #3ca41a;
  color: #f1fff3;
}
.save{
  background-color: #007BFF;
  color: white;
}
.cancel{
  color:  #007BFF;
  background-color: white;
}



